<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_material" :searchInfo="searchInfo">
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
        <!-- <FormAE ref="FormAE" :apis="apis" @refresh="$refs.list.getList()" FormsCode="oa_material" :initAfterFun="initAfterFun" />
        <FormDetails ref="FormDetails" :apis="apis" FormsCode="oa_material" /> -->
    </div>
</template>

<script>
export default {
    name: "MaterialList",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
        // FormAE: () => import("@/universalPage/formAE/FormAE.vue"),
        // FormDetails: () => import("@/universalPage/formDetails/FormDetails.vue")
